import React, { Component } from 'react';
import './App.css';
import axios from 'axios';
import moment from 'moment';
import { Badge, Card, Col, Container, Form, FormGroup, Row } from 'react-bootstrap'
import 'bootstrap/dist/css/bootstrap.min.css';
import Select from 'react-select';

  class App extends Component {  
    constructor(props) {
      super(props);
  
      this.state = {
        items: [],
        selected: "All",
        options: [],
        selectedOption: [],
        timeline: [],
      }
      this.handleChange = this.handleChange.bind(this);
      this.handleChange_Country = this.handleChange_Country.bind(this);
    }

    componentDidMount(){
      this.loadData()
    }

    getData(){
      return new Promise(resolve => {
         axios.get("https://corona-api.com/countries").then((res) => { resolve(res.data); })
      })
    }

    getTimeline(){
      return new Promise(resolve => {
         axios.get("https://corona-api.com/timeline").then((res) => { resolve(res.data); })
      })
    }
    
    loadData  = async () => {
      const res = await this.getData();
      const timeline = await this.getTimeline();
      console.log(res)
      console.log(res.data)
      console.log(timeline.data)
      let myData = res.data

      // myData.push({name: "All"})
      myData.sort((a, b) => a.name.localeCompare(b.name))
      .map((item, i) => { 
        return item;
        
      });

      let opt = [];
      myData.forEach((element,i) => {
        let dropDownEle = { label: element.name, value: element.code };
        opt.push(dropDownEle);
      });

      this.setState({items: myData, options: opt, timeline})
      console.log(myData)
    }

    handleChange(event){
      this.setState({[event.target.id]: event.target.value});
      if(event.target.id === "sortBy"){
        let myData = this.state.items.sort((a, b) => { 
          // console.log(a.name,b.name,a.latest_data[event.target.value],b.latest_data[event.target.value])
          // if(a.latest_data && b.latest_data && a.latest_data[event.target.value] && b.latest_data[event.target.value])
          return b.latest_data[event.target.value] - a.latest_data[event.target.value]
          // else
          // return 0;
        } )
        .map((item, i) => item);
        console.log(myData)
        this.setState({items: myData, selected: "All"})
      }
      if(event.target.value === "countries"){
        let myData = this.state.items.sort((a, b) => a.name.localeCompare(b.name))
        .map((item, i) => item);
        this.setState({items: myData})
      }
    }
    handleChange_Country(event){
      console.log(event)
      
      if(event)
        this.setState({selected: event.label, selectedOption: event});  
      else
        this.setState({selected: "All", selectedOption: []}); 
    }
    
    addCommas(nStr){
      nStr += '';
      var x = nStr.split('.');
      var x1 = x[0];
      var x2 = x.length > 1 ? '.' + x[1] : '';
      var rgx = /(\d+)(\d{3})/;
      while (rgx.test(x1)) {
       x1 = x1.replace(rgx, '$1' + ',' + '$2');
      }
      return x1 + x2;
     }

     formatDate(date){
      return moment(date).format('DD-MMM-YYYY LTS');
     }

  render() {
  return (
    <div className="container">
      <header className="App-header">
        <div className="jumbotron" style={{color: 'black', backgroundColor: 'darkcyan', paddingTop: '15px', paddingBottom: '15px'}}>
          {/* <div className="row" style={{color: 'black'}}> */}
        <p>
          COVID19 updates as of {this.formatDate(new Date())}
        </p>
        <Container style={{color: 'darkcyan', fontSize: 'initial'}}>
          <Card>
            <Card.Header>Worldwide</Card.Header>
            <Card.Body>
              <Row>
                <Col style={{ textAlign: 'right'}}>
                  <div>Confirmed:</div>
                </Col>
                <Col style={{textAlign: 'left'}}>
                  <div> <Badge variant="warning">{this.addCommas(this.state.timeline && this.state.timeline.data && this.state.timeline.data[0].confirmed)}</Badge>
                  {this.state.timeline && this.state.timeline.data  && this.state.timeline.data[0].new_confirmed > 0 && <Badge variant="secondary"> +{this.addCommas(this.state.timeline.data[0].new_confirmed)}</Badge>}
                </div>
                </Col>
              </Row> 
              <Row>
                <Col style={{ textAlign: 'right'}}>
                  <div>Deaths:</div>
                </Col>
                <Col style={{textAlign: 'left'}}>
                  <div>
                    <Badge variant="danger">{this.addCommas(this.state.timeline && this.state.timeline.data && this.state.timeline.data[0].deaths)}</Badge>
                    {this.state.timeline && this.state.timeline.data  && this.state.timeline.data[0].new_deaths > 0 && <Badge variant="secondary"> +{this.addCommas(this.state.timeline.data[0].new_deaths)}</Badge>}
                  </div>
                </Col>
              </Row>
              <Row>
                <Col style={{ textAlign: 'right'}}>
                  <div>Recovered:</div>
                </Col>
                <Col style={{textAlign: 'left'}}>
                  <div>
                    <Badge variant="success">{this.addCommas(this.state.timeline && this.state.timeline.data && this.state.timeline.data[0].recovered)}</Badge>
                    {this.state.timeline && this.state.timeline.data  && this.state.timeline.data[0].new_recovered > 0 && <Badge variant="secondary"> +{this.addCommas(this.state.timeline.data[0].new_recovered)}</Badge>}
                  </div>
                </Col>
              </Row>
                
            </Card.Body>
          </Card>
        </Container>
        <Row>
          <Col lg='6' sm='12'>
        <FormGroup style={{width: '-webkit-fill-available'}}>
          <label>Countries</label>
          <Select isMulti options={this.state.options} id="selected" onChange={this.handleChange_Country} value={this.state.selectedOption} />
        </FormGroup>
        </Col>
        <Col lg='6' sm='12'>
        <FormGroup style={{width: '-webkit-fill-available'}}>
          <label>Sort by</label>
          <select value={this.state.sortBy} id="sortBy" onChange={this.handleChange} style={{width: '-webkit-fill-available'}}>
            <option value="confirmed" >confirmed</option>
            <option value="deaths" >deaths</option>
            <option value="recovered">recovered</option> 
            <option default value="countries">countries</option>
          </select>
        </FormGroup>
        </Col>
        </Row>
        
        
        {/* </div> */}
        </div>
      </header> 
        <div style={{overflowY: 'scroll', flex: 1}}> 
        {this.state.selected === "All" ?
        <Card>
        {this.state.items.map((list,idx) => {
          if(list.latest_data)
          return (
            <Container style={{paddingBottom: '15px'}}>
              <Card>
                <Card.Header>{list.name}</Card.Header>
                <Card.Body>
                  <Row>
                    <Col style={{ textAlign: 'right'}}  xs='7' lg='6' md='6'>
                      <div>
                        Confirmed {' '}
                        <label>({(list.latest_data.confirmed && list.latest_data.confirmed / list.population * 100).toFixed(3)}%)</label>
                      </div>
                    </Col>
                    <Col style={{textAlign: 'left'}}>
                      <div> 
                        <Badge variant="warning">{this.addCommas(list.latest_data.confirmed)}</Badge>
                        {list.today.confirmed > 0 && <Badge variant="secondary"> +{this.addCommas(list.today.confirmed)}</Badge>}
                      </div>
                    </Col>
                  </Row> 
                  <Row>
                    <Col style={{ textAlign: 'right'}}  xs='7' lg='6' md='6'>
                      <div>Deaths {' '}
                      <label> ({list.latest_data.calculated.death_rate && list.latest_data.calculated.death_rate.toFixed(2) }%)</label>
                      </div>
                    </Col>
                    <Col style={{textAlign: 'left'}}>
                      <div>
                        <Badge variant="danger">{this.addCommas(list.latest_data.deaths)}</Badge>
                        {list.today.deaths > 0 && <Badge variant="secondary"> +{list.today.deaths}</Badge>}
                      </div>
                    </Col>
                  </Row>
                  <Row>
                    <Col style={{ textAlign: 'right'}} xs='7' lg='6' md='6'>
                      <div>Recovered {' '}
                      <label> ({list.latest_data.calculated.recovery_rate && list.latest_data.calculated.recovery_rate.toFixed(2) }%)</label>
                      </div>
                    </Col>
                    <Col style={{textAlign: 'left'}}>
                      <div>
                        <Badge variant="success">{this.addCommas(list.latest_data.recovered)}</Badge>
                        {list.today.recovered > 0 && <Badge variant="secondary"> +{list.today.recovered}</Badge>}
                      </div>
                    </Col>
                  </Row>
                </Card.Body>
              </Card>
            </Container>
            
            );
        })}
      </Card>
      :
      <div>
      {this.state.items.map((list,idx) => {
        
        if(this.state.selectedOption.some(x => x.label === list.name)){
          console.log(list)
          return (
              <Container style={{paddingBottom: '15px'}}>
              <Card>
                <Card.Header>{list.name}</Card.Header>
                <Card.Body>
                  <Row>
                    <Col style={{ textAlign: 'right'}}  xs='7' lg='6' md='6'>
                      <div>
                        Confirmed {' '}
                        <label>({(list.latest_data.confirmed && list.latest_data.confirmed / list.population * 100).toFixed(3)}%)</label>
                      </div>
                    </Col>
                    <Col style={{textAlign: 'left'}}>
                      <div> 
                        <Badge variant="warning">{this.addCommas(list.latest_data.confirmed)}</Badge>
                        {list.today.confirmed > 0 && <Badge variant="secondary"> +{this.addCommas(list.today.confirmed)}</Badge>}
                      </div>
                    </Col>
                  </Row> 
                  <Row>
                    <Col style={{ textAlign: 'right'}}  xs='7' lg='6' md='6'>
                      <div>Deaths {' '}
                      <label> ({list.latest_data.calculated.death_rate && list.latest_data.calculated.death_rate.toFixed(2) }%)</label>
                      </div>
                    </Col>
                    <Col style={{textAlign: 'left'}}>
                      <div>
                        <Badge variant="danger">{this.addCommas(list.latest_data.deaths)}</Badge>
                        {list.today.deaths > 0 && <Badge variant="secondary"> +{this.addCommas(list.today.deaths)}</Badge>}
                      </div>
                    </Col>
                  </Row>
                  <Row>
                    <Col style={{ textAlign: 'right'}} xs='7' lg='6' md='6'>
                      <div>Recovered {' '}
                      <label> ({list.latest_data.calculated.recovery_rate && list.latest_data.calculated.recovery_rate.toFixed(2) }%)</label>
                      </div>
                    </Col>
                    <Col style={{textAlign: 'left'}}>
                      <div>
                        <Badge variant="success">{this.addCommas(list.latest_data.recovered)}</Badge>
                        {list.today.recovered > 0 && <Badge variant="secondary"> +{this.addCommas(list.today.recovered)}</Badge>}
                      </div>
                    </Col>
                  </Row>
                    
                </Card.Body>
              </Card>
            </Container>
            );
        }else return <div></div>
        })}
      </div>
        }
      
      </div>
      
    </div>
  );
}}

export default App;
